.MuiFormLabel-root {
  color: black;
}

#filterOptions {
  display: flex;
  justify-content: space-between;
}

.MuiInputBase-root {
  text-overflow: ellipsis;
  width: 100px;
}

.Mui-checked {
  color: black !important;
}

.spellContainer {
  height: 90vh;
  display: flex;
}

.spellLibrary,
.spellBook {
  padding: 5px;
  overflow-y: scroll;
}

.spellLibrary::-webkit-scrollbar,
.spellBook::-webkit-scrollbar {
  display: none;
}
